.FixedLayout {
  position: fixed;
  box-sizing: border-box;
  /**
   *  WARNING 
   * `left: auto`   :
   * 1.        .
   * 2.   `SplitLayout`      `<SplitCol fixed />`.
   *
   *     https://github.com/VKCOM/VKUI/pull/2472
   *
   * ,     insets  landscape   (https://github.com/VKCOM/VKUI/issues/2422).
   */
  left: auto;
  z-index: 3;
  padding-left: var(--safe-area-inset-left);
  padding-right: var(--safe-area-inset-right);
}

.FixedLayout--filled {
  background: var(--background_content, var(--vkui--color_background_content));
}

.FixedLayout--top {
  width: 100%;
  top: 0;
}

.FixedLayout--bottom {
  width: 100%;
  bottom: 0;
  padding-bottom: var(--safe-area-inset-bottom);
}

/**
 * CMP:
 * Epic
 */
.Epic .FixedLayout--bottom {
  padding-bottom: calc(var(--tabbar_height) + var(--safe-area-inset-bottom));
}

/**
 * CMP:
 * PanelHeader
 */
.PanelHeader ~ .FixedLayout--top,
.PanelHeader ~ * .FixedLayout--top:not(.PanelHeader__fixed) {
  top: calc(var(--panelheader_height) + var(--safe-area-inset-top));
}
